@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,700&display=swap);
.map-root {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 0; }
  .map-root .leaflet-container {
    height: 100%;
    width: 100%;
    z-index: 0; }
  .map-root .incident-marker {
    background-color: #cc0202;
    border: 1px solid #cc0202;
    border-radius: 50%;
    opacity: 0.25; }

.menu-root {
  position: fixed;
  z-index: 1;
  box-shadow: 0 0 10px -2px rgba(0, 0, 0, 0.25);
  height: 100%;
  background-color: #424150;
  color: #fefffe;
  font-size: 0.9em;
  font-weight: 300;
  max-width: 350px; }
  .menu-root .menu-title,
  .menu-root .menu-content {
    padding: 10px; }
  .menu-root .menu-title {
    line-height: 1.7em;
    background-color: #33323c;
    padding: 20px 10px; }
    .menu-root .menu-title h1 {
      margin: 0; }
  .menu-root .menu-section {
    margin-bottom: 1em; }
  .menu-root .menu-section-title {
    text-transform: uppercase;
    margin: 0;
    font-weight: 700;
    font-size: 1em; }

.timeframe-picker .timeframe-picker__selector {
  width: 100%; }

.timeframe-picker .timeframe-picker__note {
  margin-top: 5px; }

.timeframe-picker .react-select__control,
.timeframe-picker .react-select__value-container,
.timeframe-picker .react-select__single-value,
.timeframe-picker .react-select__menu {
  background-color: #424150;
  color: #fefffe; }

.timeframe-picker .react-select__menu {
  background-color: #33323c;
  color: #fefffe; }

.incident-group-picker .group-checkbox {
  display: block; }

body {
  margin: 0; }

#app-root {
  font-family: 'Open Sans', sans-serif; }

